@media print {
  html {
    --highlight-color: black;
    --hover-color: black;
    --background-color: white;
    --main-color: black;
  }

  .body-header {
    margin-top: 0;
  }

  .body-aside-content, .contacts-links-not-printed {
    display: none;
  }

  .title-contacts {
    flex-direction: row;
    gap: 10px;
  }

  .title-contacts-email, .title-contacts-divider, .title-contacts-website {
    display: block;
  }

  .history-tech-stack span {
    text-decoration: none !important;
  }

  article#summary, div.history-about:nth-child(3), article#projects {
    display: none;
  }

  .header-title-job-position {
    margin-bottom: revert;
  }

  .description-details {
    display: contents;
  }

  .description-summary-title, .projects-summary-title, .description-details .history-description, .projects-details {
    display: none;
  }
}
